#page-auth {
    display: flex;
    align-items: stretch;
    height: 100vh;

    aside {
        flex: 7;

        background: #835afd;
        color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: center;

        padding: 128px 80px;

        img {
            max-width: 320px;
        }
        strong{
            font-size:36px;
            font-family: "Poppins",sans-serif;
            line-height: 32px;
            margin-top: 16px;
            
        }

        p {
            font-size: 24px;
            line-height: 32px;
            margin-top: 16px;
            color: #f8f8f8;
        }
    }

    main {
        flex: 8;
        padding: 0 32px;

        display: flex;
        justify-content: center;
        align-items: center;
    }

    .main-content {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 320px;
        align-items: stretch;
        text-align: center;

        > img {
            //Só pega a img dentro do main-content
            align-self: center;
        }
        h2{
            align-self: center;
            font-family:"Poppins", sans-serif;
        }

        form {
            input {
                height: 50px;
                border-radius: 8px;
                padding: 0 16px;
                background: #fff;
                border: 1px solid #a8a8b3;
            }

            button{
                margin-top: 16px;
            }
            button, input{
                width:100%;

            }
        }
        p{
            font-size: 14px;
            color:#737388;
            margin-top: 16px;
        }
        a{
            color:#e559f9;        
        }
    }
}

.create-room{

    margin-top: 64px;
    height: 50px;
    border-radius:8px;
    font-weight: 500;
    background: #ea4335;
    color: #FFF;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    border:0;

    transition:filter 0.2s;

    img{
        margin-right:8px;

    }

    &:hover{
        filter:brightness(0.9)
    }
}

.separator{
    font-size:14px;
    color:#a8a8b3;

    margin:32px 0;
    display: flex;
    align-items: center;

    &::before{
        content: '';
        flex: 1;
        background: #a8a8b3;
        height:1px;
        margin-right: 16px;
    }
    &::after{
        content: '';
        flex: 1;
        background: #a8a8b3;
        height:1px;
        margin-left: 16px;
    }

}

